import React from 'react'
// import TradeProvisionalComponent from './TradeFinalComponent';
import ReactToPrint from 'react-to-print-advanced';
import { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { TRADE, HEADER } from '../tradeComponent/TradeApiListFile';
import axios from 'axios';
import { useState } from 'react';
import { Print } from '@mui/icons-material';
import TradeFinalComponent from './TradeFinalComponent';

function FinalTradeLicense() {

  const componentRef = React.useRef();
  const [licenceData, setlicenceData] = useState()
  const { licenceId } = useParams();
  const [show, setshow] = useState(false)

  useEffect(() => {
    fetchLicenseData(licenceId);
  }, [licenceId])

  const fetchLicenseData = (licenseId) => {
    showLoader(true);
    axios.get(TRADE.GET_TRADE_LICENSE_DATA + licenseId)
      .then((response) => {
        console.log("License data", response.data.data);
        if (response.data.status) {
          setlicenceData(response.data.data);
          setTimeout(() => {
            showLoader(false);
          }, 500);

        } else {
          showLoader(false);
        }
      })
      .catch((error) => {
        showLoader(false);
        console.log(error);
      })
  }

  const showLoader = (val) => {
    setshow(val);
  }

  return (
    <div>

      <div>
        <ReactToPrint
          trigger={() => <button id='clickButton' className='bg-teal-500  hover:bg-teal-800 text-white px-3 py-1.5 rounded-lg text-xs font-mono'><Print/> Print this out!</button>}
          content={() => componentRef.current}
          copyStyles={true}
        />
        <TradeFinalComponent ref={componentRef} licenceData={licenceData} />
        {/* <SlipBody copyStyles={true} ref={componentRef} successData={props.successData} paymentData={props.paymentData} /> */}
      </div>
    </div>
  )
}

export default FinalTradeLicense

