import React, { useEffect, useState } from 'react'
import InitialBusinessDetails from './InitialBusinessDetails';
import FirmDetails from './FirmDetails';
import OwnerDetails from './OwnerDetails';
import Timeline from '../tradeComponent/Timeline';
import LicenseDetails from './LicenseDetails';
import DocumentUpload from './DocumentUpload';
import SuccessScreen from '../tradeComponent/SuccessScreen';
import TradeAssetComponent from '../TradeAssests/TradeAssetComponent';
import { TRADE, HEADER } from '../tradeComponent/TradeApiListFile';
import axios from 'axios';
import { convertApplicationTypeToString } from '../tradeComponent/UsefulFunctions';
import { ToastContainer, toast } from 'react-toastify';
import TradeFeedbackComponent from '../tradeComponent/TradeFeedbackComponent';
import { HiCurrencyRupee } from 'react-icons/hi';

axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';


function NewApplication(props) {

    const [collectAllFormData, setcollectAllFormData] = useState([]);
    const [FirmStep, setFirmStep] = useState(1)
    const [regCurrentStep, setRegCurrentStep] = useState(1)
    const [colorCode, setcolorCode] = useState(false)

    const [initialBusinessDetails, setinitialBusinessDetails] = useState([])
    const [firmDetails, setfirmDetails] = useState([])
    const [ownerDetails, setownerDetails] = useState([])
    const [licenseDetails, setlicenseDetails] = useState([])
    const [applicationType, setapplicationType] = useState(props.applicationType)
    const [successData, setsuccessData] = useState();
    const [paymentData, setpaymentData] = useState()

  
    // console.log('all form data is: ');
    // console.log(collectAllFormData);

    let finalData;
    const handleAllFormData = (key, formData, submitStatus) => {
        finalData = { ...collectAllFormData, [key]: formData }
        // setcollectAllFormData({ ...collectAllFormData, [key]: formData })
        setcollectAllFormData({ ...collectAllFormData, [key]: formData })

        console.log("allformdata 2", finalData);
        if (submitStatus) {
            finalSubmit(finalData)
        }

    }


    const finalSubmit = (dataToSend) => {

        let jsonTest = JSON.parse(JSON.stringify(dataToSend));
        jsonTest.applicationType = convertApplicationTypeToString(applicationType);
        jsonTest.ulbId = props?.ulbId;

        let mydata = jsonTest;
        setTimeout(() => {
            // console.log("allFormData in state", collectAllFormData)
            // console.log("allFormData", dataToSend)
            axios.post(TRADE.POST_APPLY_APPLICATION_FOR_CITIZEN, Object.assign({}, mydata), HEADER())
                .then(function (response) {
                    console.log("response ", response.data);
                    if (response.data.status) {

                        notify("Application Submitted Successfully !", "success");
                        setsuccessData(response.data);
                        getPaymentDetailsForReceipt(response.data);
                        setFirmStep(5)
                    }
                    else {
                        setFirmStep(4);
                        notify("Something went wrong !", "notice");
                    }
                    // props.showLoader(false);                                              
                })
                .catch(function (error) {
                    console.log(error);
                    // alert("Invalid response received");
                    notify("Invalid response received !", "error");
                    props.showLoader(false);
                })
        }, 500)
    }

  


    const values = {
        applicationType: props.applicationType,
        colorCode: colorCode,
        currentStep: regCurrentStep,
        currentStepFun: setRegCurrentStep,
        collectFormDataFun: handleAllFormData,
        firmStepFun: setFirmStep,
        firmStep: FirmStep,
        colorCodeFun: setcolorCode,
        fieldData: props.fieldData,
        allFormData: collectAllFormData,
        showLoader: props.showLoader,
        finalSubmit: finalSubmit,
        ulbId: props?.ulbId,

    };

    const successValues = {
        successData: successData,
        successFun: setsuccessData
    }
    console.log('values ', values);

    const getPaymentDetailsForReceipt = (data) => {
        console.log("===getting payment data====", data.data.paymentRecipt);
        axios.get(data.data.paymentRecipt)
            .then(function (response) {
                if (response.data.status) {
                    setpaymentData(response.data.data)
                    console.log("===getting payment data thorugh api====", response.data);
                }
            })
            .catch(function (error) {
                console.log("===getting payment data thorugh api erro====", error);
            })
    }

    const [denialDetails, setdenialDetails] = useState({});

    const notify = (toastData, actionFlag) => {
        toast.dismiss();
        { actionFlag == 'escalated' && toast.success(toastData) }
        { actionFlag == 'success' && toast.success(toastData) }
        { actionFlag == 'de-escalated' && toast.warn(toastData) }
        { actionFlag == 'notice' && toast.warn(toastData) }
        { actionFlag == 'error' && toast.error(toastData) }
    };

   

    


    return (
        <div>
            <div className=''>
                <TradeAssetComponent applicationType={props.applicationType} ulbName={props?.ulbName} />
            </div>
            <div className='grid grid-cols-12 h-auto border '>

                <div className='col-span-12 md:col-span-9 pt-4 border-l-8 border-white bg-white mt-1 md:mt-0 relative '>
                    <div className=" overflow-x-clip ">
                        {/* Form stepper for New trade License */}
                        {/* {FirmStep == 5 ? '' : <> <div className='w-full'>
                            <Timeline colorCode={colorCode} currentStep={regCurrentStep} /></div><p className='w-full border-b mt-12'></p></>} */}

                        <div className={`mt-1 border border-gray-50 mr-2 pl-5 transition-all ${FirmStep == 1 ? 'translate-x-0' : 'translate-x-full'}`}><InitialBusinessDetails values={values} denialDetailsFun={setdenialDetails} toast={notify} /> </div>
                        <div className={`mt-8 mr-2 pl-5  transition-all ${FirmStep == 2 ? ' translate-x-0  ' : 'translate-x-full'}`}><FirmDetails values={values} denialDetails={denialDetails} toast={notify} /></div>
                        <div className={`mt-8 mr-2 pl-5 transition-all ${FirmStep == 3 ? ' translate-x-0  ' : 'translate-x-full'}`}><OwnerDetails values={values} toast={notify} /></div>
                        <div className={`mt-8 mr-2 pl-5 transition-all ${FirmStep == 4 ? ' translate-x-0  ' : 'translate-x-full'}`}><LicenseDetails values={values} successData={successValues} toast={notify} /></div>
                        {/* <div className={`mt-8 transition-all ${FirmStep == 5 ? ' translate-x-0  ' : 'translate-x-full'}`}>
                    <DocumentUpload values={values} />
                </div> */}
                        <div className={`mt-8 transition-all ${FirmStep == 5 ? ' translate-x-0  ' : 'translate-x-full'}`}><SuccessScreen values={values} successData={successValues} paymentData={paymentData} toast={notify} /></div>
                    </div>

                </div>

                <div className='hidden md:block col-span-12 md:col-span-3 pt-4 border-l-8 bg-white border-white mt-10 md:mt-0  relative h-screen overflow-y-auto'>
                    <TradeFeedbackComponent allFormData={collectAllFormData} />
                </div>

            </div>
            {/* ----------------*-----------------------------------*------------------------------ */}
          

            {/* ----------------*-----------------------------------*------------------------------ */}
        </div>
    )
}

export default NewApplication