import { useState, useEffect } from 'react'
// import MailboxSidebar from 'Components/Common/MailboxSidebar'
import NewApplication from './NewApplication';
// import { contextVar } from '.././../Components/Context/Context';
import axios from 'axios';
// import ApplicationList from './NewApplication/ApplicationList';
// import Renewal from './Renewal/Renewal';
// import Surrender from './Surrender/Surrender';
// import Amendment from './Amendment/Amendment';
// import UpdateApplicationMain from './tradeComponent/UpdateApplicationMain';
import { TRADE, HEADER } from '../tradeComponent/TradeApiListFile';
import CircularProgress from '@mui/material/CircularProgress';
import { ThreeCircles } from 'react-loader-spinner';
import TradeLoader from '../tradeComponent/TradeLoader';
import { convertApplicationTypeToString } from '../tradeComponent/UsefulFunctions';
import NotificationCard from '../NotificationCard';
import { ToastContainer, toast } from 'react-toastify';
import Modal from "react-modal";
import { LocationCity } from '@mui/icons-material';

function NewApplicationEntry() {
    const [tabIndex, settabIndex] = useState(0)     //state to store current tab index
    const [idApi, setidApi] = useState(0)
    const [updateData, setupdateData] = useState(
        {
            category: "Not Authenticated",
            created_at: "2022-08-02 05:37:55",
            created_by: "Anshu Kumar",
            created_on: "2022-06-29 00:00:00",
            description: "abc",
            discontinued: false,
            end_point: "test2",
            id: 3,
            post_condition: "None",
            pre_condition: "Authentication is required",
            remarks: "",
            request_payload: "Token Only",
            response_payload: "username,password,userid",
            revision_no: 1,
            tags: "tag5,tag6",
            updated_at: "2022-08-02 05:37:55",
            usage: "For Saving API Keys",
            version: "Version 1.0",
        });

    // application type state
    const [applicationType, setapplicationType] = useState();

    // state to store master-data
    const [getSelectFieldData, setgetSelectFieldData] = useState([]);

    const [pointerEvent, setpointerEvent] = useState('pointer-events-none opacity-50');
    const [loaderShow, setloaderShow] = useState('hidden');
    const [show, setshow] = useState(false);

    const [ulbId, setulbId] = useState(2);
    const [ulbList, setulbList] = useState();
    const [ulbName, setulbName] = useState('');
    const [modalIsOpen, setIsOpen] = useState(true);

    // LICENSE DATA FOR RENEWAL AMENDMENT OR SURRENDER 
    const [licenseDataForRAS, setlicenseDataForRAS] = useState([]);

    let loginType = 1;

    const customStyles = {
        content: {
            top: "50%",
            left: "50%",
            right: "auto",
            bottom: "auto",
            marginRight: "-50%",
            transform: "translate(-50%, -50%)",
            background: "transparent",
            border: "none",
        },
    };

    function afterOpenModal() { }

    function closeModal() {
        setIsOpen(false);
    }
    const handleSelectedUlb = (e) => {
        let ulbName = e.target.selectedOptions[0].text;
        let ulbId = e.target.selectedOptions[0].value;

        setulbId(ulbId);
        setulbName(ulbName);

    }
    const confirmUlb = () => {
        if (ulbId == '' || ulbId == null || ulbId == undefined) {
            setulbId(2);
        }
        notify("ULB Selected", "success");
        closeModal()
    }

    const setapplicationTypeFun = (index) => {
        setapplicationType(index);
    }

    const tabs = [
        { title: "New Application", tabIndex: 0 },
        { title: "Renewal", tabIndex: 1 },
        { title: "Surrender", tabIndex: 2 },
        { title: "Amendment", tabIndex: 3 },
        { title: "Application List", tabIndex: 4 },
    ]
    const tabSwitch = (index) => {
        //tabSwitch function receive tabIndex to switch between tabs called from Sidebar menu
        settabIndex(index)      //updating the tab index to recent value
        console.log('index called ', index)
    }

  

    // function to get master data for select fields in New Trade application form
    const getSelectFieldDataFun = () => {
        showLoader(true)
        console.log("api url", HEADER())
        axios.post(TRADE.MASTER_DATA_API, { applicationType: convertApplicationTypeToString(applicationType), ulbId: ulbId }, HEADER())
            .then(function (response) {

                setgetSelectFieldData(response.data.data);
                setpointerEvent('');    //readonly fields
                showLoader(false)
            })
            .catch(function (error) {
                console.log(error);
                console.log(error)
                showLoader(false)
            })
    }

    // Calling the function to get master-data
    useEffect(() => {
        getUlbList();

    }, [ulbId])
    const getUlbList = () => {
        axios.get(TRADE.GET_ULB_LIST, HEADER())
            .then((response) => {
                console.log("ulb list", response.data);
                if (response.data.status) {
                    setulbList(response.data.data)
                    getSelectFieldDataFun()
                } else {

                }
            })
            .catch((error) => {
                console.log("ulb list", error);
            })
    }

    useEffect(() => {
        getUlbList()
    }, [])
    // set loader visibility
    const showLoader = (value) => {
        console.log('loader is called with value ', value)
        setshow(value);
    }

    //sets searched license data to be applied on the renewal, amedment and surrender process
    const setLicenseDataForRasFun = (values) => {
        console.log("searched license data", values);
        setlicenseDataForRAS(values);
    }
    const licenseValues = {
        licenseDetailsFun: setLicenseDataForRasFun,
        licenseData: licenseDataForRAS
    }

    const notify = (toastData, actionFlag) => {
        toast.dismiss();
        { actionFlag == 'escalated' && toast.success(toastData) }
        { actionFlag == 'success' && toast.success(toastData) }
        { actionFlag == 'de-escalated' && toast.warn(toastData) }
        { actionFlag == 'notice' && toast.warn(toastData) }
        { actionFlag == 'error' && toast.error(toastData) }
    };

    return (
        <>
            <div className='py-12'>
                <TradeLoader show={show} />
                {/* <div className='grid grid-cols-12'> */}
                {/* <div className='col-span-12 md:col-span-9  overflow-y-scroll' style={{ 'height': '90vh' }}></div> */}
                <NewApplication fieldData={getSelectFieldData} showLoader={showLoader} applicationType="1" title="NewApplication" toast={notify} ulbId={ulbId} ulbName={ulbName} />

                <div>
                    <Modal
                        isOpen={modalIsOpen}
                        onAfterOpen={afterOpenModal}
                        onRequestClose={closeModal}
                        style={customStyles}
                        contentLabel="Example Modal"
                        shouldCloseOnOverlayClick={false}
                    >
                        <div className="bg-gradient-to-tr from-white to-white text-gray-600 shadow-2xl border border-sky-200 p-5 m-5 rounded-md" style={{ minWidth: "49vw", minHeight: "40vh" }}>
                            <div className='w-full'>
                                <div className='border-b-2border-red-400 py-1 pl-3'>

                                    <div className='  font-semibold text-lg  flex justify-start'><LocationCity size={20} className='mt-0.5 mr-1' />
                                        <p className='block'> SELECT YOUR ULB</p>
                                    </div>
                                    <p className='text-xs block'>You need to select the ulb , where your firm is operating.</p>
                                </div>
                                <div className="">
                                    <select name="ulbName" id="" className='border bg-white text-gray-600 space-y-2 hover:bg-amber-50 w-full px-2 py-2 my-4 rounded-md' onChange={handleSelectedUlb} required>
                                        <option value="">SELECT ULB</option>
                                        {ulbList?.map((item) => (
                                            <option value={item.id}>{item.ulb_name}</option>
                                        ))}
                                    </select>

                                    <div className='mb-2 block'>
                                        {ulbName != '' &&
                                            <p>You have selected
                                                <span className='text-blue-600'> {ulbName}</span>
                                            </p>
                                        }
                                    </div>
                                    <button className='inline-block px-6 py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out w-full' onClick={confirmUlb}>Confirm</button>
                                </div>

                            </div>
                        </div>
                    </Modal>
                </div>

                {/* </div> */}

            </div>
        </>
    );

}
export default NewApplicationEntry;