
// import QrTesting from 'Pages/Trade/tradeComponent/QrTesting'
import React from 'react'
import { Route, Routes } from 'react-router-dom'
import AmendmentApplicationEntry from '../Amendment/AmendmentApplicationEntry'
import FinalTradeLicense from '../FinalTradeLicense/FinalTradeLicense'
import TradeDocumentUpload from '../Modals/TradeDocumentModal'
import NewApplicationEntry from '../NewApplication/NewApplicationEntry'
import RenewalApplicationEntry from '../Renewal/RenewalApplicationEntry'
import SurrenderApplicationEntry from '../Surrender/SurrenderApplicationEntry'
import Trade from '../Trade'
import AmendmentList from '../tradeComponent/AmendmentList'
import ProvisionalLicense from '../tradeComponent/ProvisionalLicense'
import RenewalList from '../tradeComponent/RenewalList'
import SurrenderList from '../tradeComponent/SurrenderList'
import TradeDocumentUploadUrl from '../tradeComponent/TradeDocumentUploadUrl'
import TradePaymentReceipt from '../tradeComponent/TradePaymentReceipt'
import TradeViewApplicationById from '../TradeViewApplicationById'

function TradeRoutesByAnshuman() {
    return (
        <Routes>
            <Route path='/trade-new-apply' element={<NewApplicationEntry />} />

            <Route path='/trade-renewal-list' element={<RenewalList />} />
            <Route path='/trade-renewal/:licenceId' element={<RenewalApplicationEntry />} />

            <Route path='/trade-amendment/:licenceId' element={<AmendmentApplicationEntry />} />
            <Route path='/trade-amendment-list' element={<AmendmentList />} />

            <Route path='/trade-surrender/:licenceId' element={<SurrenderApplicationEntry />} />
            <Route path='/trade-surrender-list' element={<SurrenderList />} />

            <Route path='/trade' element={<Trade />} />
            {/* <Route path='/tradeDashboard' element={<TradeDashboardNew />} /> */}
            <Route path='/trade-view/:licenceId' element={<TradeViewApplicationById />} />
            <Route path='/trade-document-upload/:id' element={<TradeDocumentUploadUrl />} />
            <Route path='/trade-payment-receipt/:id/:tranId' element={<TradePaymentReceipt />} />
            <Route  path="/trade-provisional-license/:licenceId"  element={<ProvisionalLicense />}  />

            <Route  path="/trade-approved-license/:licenceId"  element={<FinalTradeLicense />}  />
        </Routes>
    )
}

export default TradeRoutesByAnshuman
