
import axios from 'axios'
import React, { useState } from 'react'
import { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { HEADER, TRADE } from './TradeApiListFile'
import TradeNonBlockingLoader from './TradeNonBlockingLoader'

function SurrenderList() {
  const [renewalList, setrenewalList] = useState([])
  const [show, setshow] = useState(false)

  const getRenewalList = () => {
    showLoader(true)
    axios.post(TRADE.GET_SURRENDERABLE_LICENSE_LIST, {}, HEADER())
      .then((response) => {
        console.log("surrender list", response.data)
        if (response.data.status) {
          setrenewalList(response.data.data);
          showLoader(false);
        } else {
          showLoader(false);
        }
      })
      .catch((error) => {
        console.log(error);
        showLoader(false);
      })
  }

  useEffect(() => {
    getRenewalList()
  }, [])


  const showLoader = (val) => {
    setshow(val)
  }
  return (
    <div>
      <TradeNonBlockingLoader show={show} />
      <div className='w-full bg-white'>
        <div class="flex flex-col">
          <div class="overflow-x-auto sm:-mx-6 lg:-mx-8">
            <div class="py-4 inline-block min-w-full sm:px-6 lg:px-8">
              <div class="overflow-hidden m-4">
                <h1 className='h-12 rounded w-full bg-violet-100 px-4 py-2 my-4 text-lg font-semibold text-gray-600'>Licenses eligible for surrender</h1>
                {renewalList?.length > 0 ? <>
                  <table class="w-full text-center">
                    <thead class="border-b bg-violet-50">
                      <tr>
                        <th scope="col" class="text-sm font-medium text-gray-900 px-6 py-4">
                          #
                        </th>
                        <th scope="col" class="text-sm font-medium text-gray-900 px-6 py-4">
                          License No.
                        </th>
                        <th scope="col" class="text-sm font-medium text-gray-900 px-6 py-4">
                          Application No.
                        </th>
                        <th scope="col" class="text-sm font-medium text-gray-900 px-6 py-4">
                          Firm Name
                        </th>
                        <th scope="col" class="text-sm font-medium text-gray-900 px-6 py-4">
                          Apply Date.
                        </th>
                        <th scope="col" class="text-sm font-medium text-gray-900 px-6 py-4">
                          Valid From
                        </th>
                        <th scope="col" class="text-sm font-medium text-gray-900 px-6 py-4">
                          Valid Upto
                        </th>
                        <th scope="col" class="text-sm font-medium text-gray-900 px-6 py-4">
                          Action
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      {renewalList?.map((items) => (

                        <tr class="bg-white border-b hover:bg-violet-50">
                          <td class="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">1</td>
                          <td class="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {items?.license_no}
                          </td>
                          <td class="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {items?.application_no}
                          </td>
                          <td class="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {items?.firm_name}
                          </td>
                          <td class="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {items?.application_date}
                          </td>
                          <td class="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {items?.valid_from}
                          </td>
                          <td class="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {items?.valid_upto}
                          </td>

                          <td class="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            <button className='inline-block px-6 py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out'><Link to={`/trade-surrender/${items?.id}`}>Apply For Surrender</Link></button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </> : <>
                  <div className='w-64 mx-auto text-center'>
                    <img src="https://cdn-icons-png.flaticon.com/512/7465/7465691.png" alt="" />
                    <h1>You have no active licenses for Surrender</h1>
                  </div>
                </>}
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  )
}

export default SurrenderList