import React from 'react'

import { FaHome } from 'react-icons/fa'
import { useNavigate } from 'react-router-dom'
function InfoComponentStepper(props) {

    
    return (
        <>
            <div className='h-8 flex justify-start'>
                <h1 className=' font-serif font-semibold  text-gray-600 text-xs md:text-lg'>
                    <FaHome className="inline mr-2 "/>{props?.heading} </h1>
                    
                <p className='ml-4 italic font-mono text-[10px] text-slate-500 capitalize  md:h-6 bg-slate-100 rounded-lg w-auto flex'>
                    <span className='text-amber-600'>
                        <img className="w-4 h-4 opacity-75 mt-1" src="https://cdn-icons-png.flaticon.com/512/3766/3766220.png" alt="application type" title={`${props?.iconTitle}`} />
                    </span>
                    <span className='p-1'>{props?.message} </span>
                </p>

            </div>
        </>
    )
}

export default InfoComponentStepper