import { DocumentScanner } from '@mui/icons-material'
import React from 'react'
import BackendUrl from '../../../Components/ApiList/BackendUrl'

function TradeDocumentRow(props) {

    // console.log("doclist", props?.docList)
    return (
        <>
            <tr className='text-left'>

                <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                    <p className="text-gray-900 whitespace-no-wrap">
                        {props?.index + 1}
                    </p>
                </td>
                <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                    <p className="text-gray-900 whitespace-no-wrap">
                        {props?.docList?.doc_for}
                    </p>
                </td>
                <td className="px-5 py-2 border-b border-gray-200 bg-white" >
                    <div className="flex items-center " >
                        <div className="flex-shrink-0 text-green-500 cursor-pointer" onClick={() => props?.modalAction(`${BackendUrl}/${props?.docList?.doc_path}`)}>
                            {/* <a href="#" className="block relative"> */}
                            {/* {`${BackendUrl}/RMCDMC/public/assets/img/pdf_logo.png`} */}
                            {/* <img alt="profil" src={`${BackendUrl}/RMCDMC/public/assets/img/pdf_logo.png`} className="mx-auto object-cover rounded-full h-10 w-10 cursor-pointer" /> */}
                            <DocumentScanner />
                            {/* </a> */}
                        </div>
                    </div>
                </td>
                <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                    <p className="text-red-900 whitespace-no-wrap">

                        {/* {props?.docList?.verify_status == 1 && "Verified"}
                        {props?.docList?.verify_status == 2 && "Rejected"}
                        {props?.docList?.verify_status == 0 && "Pending"} */}
                        {props?.docList?.verify_status == 1 && <span className='text-emerald-400'>Verified</span>}
                        {props?.docList?.verify_status == 2 && <span className='text-red-400'>Rejected</span>}
                        {props?.docList?.verify_status == 0 && <span className='text-amber-400'>Pending</span>}
                    </p>
                </td>
                {/* <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                    <span className="relative inline-block px-3 py-1 font-semibold text-sky-900 leading-tight">
                        <span aria-hidden="true" className="absolute inset-0 bg-sky-200 opacity-50 rounded-full">
                        </span>
                        <span className="relative">
                            Upload Fresh Document
                        </span>
                    </span>
                </td> */}
            </tr>

        </>
    )
}

export default TradeDocumentRow
