
import React from 'react'
// import ThemeStyle from '../../Components/Styles/ThemeStyle'

function TradeFeedbackComponent(props) {

    const { initialBusinessDetails, firmDetails, ownerDetails } = props?.allFormData;

    console.log("applyWith", props?.allFormData)
    return (
        <div className=''>

            <div className='p-2 items-center  overflow-hidden mx-auto bg-white  font-mono text-xs'>
                <h1 className='font-bold text-lg text-slate-500 my-2 bg-stone-100 py-1.5 uppercase font-mono text-center'>Your Selected Options</h1>
                Basic Details :
                {initialBusinessDetails?.applyWith != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-50 hover:bg-slate-200'>
                        <div className='py-2'> <p>Apply With : </p> </div>
                        <div>
                            <p className='py-2'>
                                {initialBusinessDetails?.applyWith == 1 && " Notice No"}
                                {initialBusinessDetails?.applyWith == 2 && " New Application"}
                                {/* {initialBusinessDetails?.applyWith == '' && "..."} */}
                            </p>
                        </div>
                    </div>
                }
                {initialBusinessDetails?.applyWith != '' && initialBusinessDetails?.applyWith == 1 &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-100 hover:bg-slate-200'>
                        <div className='py-2'> <p>Notice No :</p> </div>
                        <div> <p className='py-2'>{initialBusinessDetails?.noticeNo}</p></div>
                    </div>
                }
                {initialBusinessDetails?.noticeDate != '' && initialBusinessDetails?.applyWith == 1 &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-50 hover:bg-slate-200'>
                        <div className='py-2'> <p>Notice Date :</p> </div>
                        <div> <p className='py-2'>{initialBusinessDetails?.noticeDate}</p></div>
                    </div>
                }

                {/* Firm name Section */}
                {firmDetails?.firmName != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-100 hover:bg-slate-200'>
                        <div className='py-2'> <p>Firm Name :</p> </div>
                        <div> <p className='py-2'>{firmDetails?.firmName}</p></div>
                    </div>
                }

                {/* Area In SQFT. Section */}
                {firmDetails?.areaSqft != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-50 hover:bg-slate-200'>
                        <div className='py-2'> <p>Plot Area (in Sqft.) :</p> </div>
                        <div> <p className='py-2'>{firmDetails?.areaSqft}</p></div>
                    </div>
                }

                {/* OwnerShip Type Section */}
                {initialBusinessDetails?.onwershipType != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-100 hover:bg-slate-200'>
                        <div className='py-2'> <p>onwershipType :</p> </div>
                        <div>
                            <p className='py-2'>
                                {initialBusinessDetails?.ownershipType == 1 && "On Rent"}
                                {initialBusinessDetails?.ownershipType == 2 && "On Lease"}
                                {initialBusinessDetails?.ownershipType == 3 && "Own Property"}
                            </p>
                        </div>
                    </div>
                }

                {/* categoryType  Section */}
                {initialBusinessDetails?.categroyTypeId != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-50 hover:bg-slate-200'>
                        <div className='py-2'> <p>categoryType  :</p> </div>
                        <div>
                            <p className='py-2'>
                                {initialBusinessDetails?.categroyTypeId == 1 && "H/L/B/D"}
                                {initialBusinessDetails?.categroyTypeId == 2 && "Dangerous Trade"}
                                {initialBusinessDetails?.categroyTypeId == 3 && "Others"}
                                {initialBusinessDetails?.categroyTypeId == '' && "..."}
                            </p>
                        </div>
                    </div>
                }
                {/* ward Type Section */}
                {firmDetails?.wardNo != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-100 hover:bg-slate-200'>
                        <div className='py-2'> <p>Plot Area (in Sqft.) :</p> </div>
                        <div> <p className='py-2'>{firmDetails?.wardNo}</p></div>
                    </div>
                }
                {firmDetails?.newwardNo != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-50 hover:bg-slate-200'>
                        <div className='py-2'> <p>newwardNo :</p> </div>
                        <div> <p className='py-2'>{firmDetails?.newWardNo}</p></div>
                    </div>
                }


                {/* propertyDetails Section */}
                {firmDetails?.holdingNo != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-100 hover:bg-slate-200'>
                        <div className='py-2'> <p>holdingNo :</p> </div>
                        <div> <p className='py-2'>{firmDetails?.holdingNo}</p></div>
                    </div>
                }
                {firmDetails?.premisesOwner != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-50 hover:bg-slate-200'>
                        <div className='py-2'> <p>premisesOwner :</p> </div>
                        <div> <p className='py-2'>{firmDetails?.premisesOwner}</p></div>
                    </div>
                }
                {firmDetails?.businessAddress != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-100 hover:bg-slate-200'>
                        <div className='py-2'> <p>businessAddress :</p> </div>
                        <div> <p className='py-2'>{firmDetails?.businessAddress}</p></div>
                    </div>
                }
                {firmDetails?.landmark != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-50 hover:bg-slate-200'>
                        <div className='py-2'> <p>landmark :</p> </div>
                        <div> <p className='py-2'>{firmDetails?.landmark}</p></div>
                    </div>
                }
                {firmDetails?.pincode != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-100 hover:bg-slate-200'>
                        <div className='py-2'> <p>pincode :</p> </div>
                        <div> <p className='py-2'>{firmDetails?.pincode}</p></div>
                    </div>
                }

                {/* businessDescription Section */}
                {firmDetails?.businessDescription != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b bg-slate-50 hover:bg-slate-200'>
                        <div className='py-2'> <p>businessDescription: </p><p className='py-2'> {firmDetails?.businessDescription}</p> </div><br />
                        {/* <div> <p className='py-2'> {firmDetails?.businessDescription}</p></div> */}
                    </div>
                }

                Owner Details :
                {ownerDetails?.length > 0 && ownerDetails?.map((data, index) => (

                    <>
                        <div className=' flex justify-between  my-2 px-4 border-b bg-slate-300 hover:bg-slate-200'>
                            <div className='font-bold text-gray-600'> <p>owner  :{index + 1}</p> </div>
                            {/* <div> <p className='py-2'>{data?.businessOwnerName}</p></div> */}
                        </div>
                        <div className=' flex justify-between  my-2 px-4 border-b  bg-slate-50  hover:bg-slate-200'>
                            <div className='py-2'> <p>ownerName :</p> </div>
                            <div> <p className='py-2'>{data?.businessOwnerName}</p></div>
                        </div>


                        <div className=' flex justify-between  my-2 px-4 border-b  bg-slate-100  hover:bg-slate-200'>
                            <div className='py-2'> <p>guardianName :</p> </div>
                            <div> <p className='py-2'>{data?.guardianName}</p></div>
                        </div>

                        <div className=' flex justify-between  my-2 px-4 border-b  bg-slate-50  hover:bg-slate-200'>
                            <div className='py-2'> <p>mobile :</p> </div>
                            <div> <p className='py-2'>{data?.mobileNo}</p></div>
                        </div>


                        <div className=' flex justify-between  my-2 px-4 border-b  bg-slate-100  hover:bg-slate-200'>
                            <div className='py-2'> <p>email :</p> </div>
                            <div> <p className='py-2'>{data?.email}</p></div>
                        </div>

                    </>
                ))}

                {/** licenseFor Section
                {licenseFor?.licenseForYears != '' &&
                    <div className=' flex justify-between  my-2 px-4 border-b'>
                        <div className=''> <p>licenseForYears :</p> </div>
                        <div> <p className=''>{licenseFor.licenseForYears}</p></div>
                    </div>
                } */}
            </div>

        </div>
    )
}

export default TradeFeedbackComponent